/**
 *      
 * @param {string} question -  
 * @param {number|null} conversationId -  ID (null  )
 * @param {Array} conversationHistory -   ()
 * @returns {Object}  
 */
export function judgeQuestionType(question, conversationId, conversationHistory = []) {
    // console.log('[QUESTION_JUDGE]    ');
    // console.log('[QUESTION_JUDGE] - :', question);
    // console.log('[QUESTION_JUDGE] -  ID:', conversationId);
    // console.log('[QUESTION_JUDGE] -  :', conversationHistory.length);

    // 1. conversationId null undefined   ( )
    if (!conversationId) {
        // console.log('[QUESTION_JUDGE]    -   ');
        return {
            isFirstQuestion: true,
            questionType: 'first',
            apiEndpoint: '/api/langgraph/stream',
            reason: ' '
        };
    }

    // 2.     
    if (!conversationHistory || conversationHistory.length === 0) {
        console.log('[QUESTION_JUDGE]    -   ');
        return {
            isFirstQuestion: true,
            questionType: 'first',
            apiEndpoint: '/api/langgraph/stream',
            reason: '  '
        };
    }

    // 3. conversationId      
    // (LangGraph        )
    console.log('[QUESTION_JUDGE]     -   ');
    console.log('[QUESTION_JUDGE] -  ID:', conversationId);
    console.log('[QUESTION_JUDGE] -   :', conversationHistory.length);
    
    return {
        isFirstQuestion: false,
        questionType: 'followup',
        apiEndpoint: '/api/normal_llm/langgraph/followup/stream',
        reason: '   '
    };
}

/**
 *   API  
 * @param {string} question -  
 * @param {number|null} conversationId -  ID
 * @param {Array} conversationHistory -  
 * @param {Object} options -  
 * @returns {Promise} API 
 */
export async function sendQuestionToAppropriateEndpoint(question, conversationId, conversationHistory = [], options = {}) {
    console.log('[QUESTION_ROUTER]   ');
    
    //   
    const judgment = judgeQuestionType(question, conversationId, conversationHistory);
    
    console.log('[QUESTION_ROUTER]  :', judgment);
    
    // API   
    const requestData = {
        question: question,
        conversation_id: conversationId,
        generate_image: options.generateImage || false,
        include_langgraph_context: !judgment.isFirstQuestion, //     
        langgraph_context: !judgment.isFirstQuestion ? {
            documents: conversationHistory
                .filter(msg => msg.langgraph_result?.documents)
                .flatMap(msg => msg.langgraph_result.documents)
                .slice(0, 5) //  5 
        } : null
    };

    console.log('[QUESTION_ROUTER]  :', requestData);
    console.log('[QUESTION_ROUTER] API :', judgment.apiEndpoint);

    try {
        //    
        const response = await fetch(judgment.apiEndpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${localStorage.getItem('token') || ''}`
            },
            body: JSON.stringify(requestData)
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        console.log('[QUESTION_ROUTER]  API  ');
        
        return {
            response: response,
            judgment: judgment,
            requestData: requestData
        };

    } catch (error) {
        console.error('[QUESTION_ROUTER]  API  :', error);
        throw error;
    }
}

/**
 *   LangGraph  
 * @param {Array} conversationHistory -  
 * @returns {Object} LangGraph 
 */
export function extractLangGraphContext(conversationHistory) {
    console.log('[CONTEXT_EXTRACTOR] LangGraph   ');
    
    const context = {
        documents: [],
        keywords: [],
        hasSearchResults: false
    };

    if (!conversationHistory || conversationHistory.length === 0) {
        console.log('[CONTEXT_EXTRACTOR]  ');
        return context;
    }

    // LangGraph    
    for (const msg of conversationHistory) {
        if (msg.langgraph_result?.documents) {
            context.documents.push(...msg.langgraph_result.documents);
            context.hasSearchResults = true;
        }
        
        if (msg.keyword) {
            try {
                const keywords = typeof msg.keyword === 'string' 
                    ? JSON.parse(msg.keyword) 
                    : msg.keyword;
                if (Array.isArray(keywords)) {
                    context.keywords.push(...keywords);
                }
            } catch (e) {
                console.warn('[CONTEXT_EXTRACTOR]   :', e);
            }
        }
    }

    //    
    context.documents = context.documents.slice(0, 5);
    context.keywords = [...new Set(context.keywords)].slice(0, 10);

    console.log('[CONTEXT_EXTRACTOR]  :', {
        documentsCount: context.documents.length,
        keywordsCount: context.keywords.length,
        hasSearchResults: context.hasSearchResults
    });

    return context;
}

/**
 *    UI  
 * @param {Object} judgment -  
 * @param {Function} updateUI - UI  
 */
export function updateUIForQuestionType(judgment, updateUI) {
    console.log('[UI_UPDATER] UI  :', judgment);
    
    if (judgment.isFirstQuestion) {
        //   UI
        updateUI({
            showLangGraphProgress: true,
            showSearchResults: true,
            questionType: 'first',
            statusMessage: 'RAG    ...'
        });
    } else {
        //   UI
        updateUI({
            showLangGraphProgress: false,
            showSearchResults: false,
            questionType: 'followup',
            statusMessage: '    ...'
        });
    }
}

/**
 *     
 * @param {Error} error -  
 * @param {Object} judgment -   
 * @returns {Object}   
 */
export function handleQuestionError(error, judgment) {
    console.error('[ERROR_HANDLER]   :', error);
    
    // LangGraph    LLM 
    if (judgment.isFirstQuestion && error.message.includes('langgraph')) {
        console.log('[ERROR_HANDLER] LangGraph  -  LLM ');
        return {
            ...judgment,
            isFirstQuestion: false,
            questionType: 'fallback',
            apiEndpoint: '/api/chat/stream',
            reason: 'LangGraph   '
        };
    }
    
    //   
    return {
        error: error.message,
        fallback: true,
        apiEndpoint: '/api/chat/stream'
    };
}

export default {
    judgeQuestionType,
    sendQuestionToAppropriateEndpoint,
    extractLangGraphContext,
    updateUIForQuestionType,
    handleQuestionError
};
