// OAuth     
let isProcessingOAuth = false;
let hasProcessedOAuth = false; // OAuth     

// OAuth   
function resetOAuthFlags() {
  isProcessingOAuth = false;
  hasProcessedOAuth = false;
  sessionStorage.removeItem('oauth_processing');
  sessionStorage.removeItem('sso_processed');
  // // console.log('[AUTH] OAuth  ');
}

const requireAuth = (to, from, next) => {
  // OAuth     
  if (to.path === '/oauth_callback') {
    next();
    return;
  }
  
  // OAuth       
  if (isProcessingOAuth) {
    // OAuth    ( 3 )
    let waitCount = 0;
    const maxWait = 15; // 3 (200ms * 15)
    
    const checkAuth = () => {
      waitCount++;
      
      //    
      if (!isProcessingOAuth || waitCount >= maxWait) {
        //   OAuth   
        if (waitCount >= maxWait) {
          // // console.log('[AUTH] OAuth   -   ');
          isProcessingOAuth = false;
          sessionStorage.removeItem('oauth_processing');
        }
        
        //   
        const storedToken = localStorage.getItem('access_token');
        const storedUserInfo = localStorage.getItem('user_info');
        
        if (storedToken && storedUserInfo && store.state.isAuthenticated) {
          // // console.log('[AUTH]   -   ');
          next();
        } else {
          // // console.log('[AUTH]   - samsung SSO ');
          next(false); //      
          // samsung SSO 
          setTimeout(() => {
            try {
              window.location.replace('http://localhost:8000/api/auth/auth_sh');
            } catch (error) {
              try {
                window.location.href = 'http://localhost:8000/api/auth/auth_sh';
              } catch (error2) {
                console.error('SSO  :', error2);
              }
            }
          }, 100);
        }
      } else {
        setTimeout(checkAuth, 200); //   200ms 
      }
    };
    checkAuth();
    return;
  }
  
  //      (store   localStorage )
  if (store.state.isAuthenticated && store.state.token) {
    next();
    return;
  }
  
  // localStorage   (store  )
  const storedToken = localStorage.getItem('access_token');
  const storedUserInfo = localStorage.getItem('user_info');
  
  if (storedToken && storedUserInfo) {
    try {
      const userData = JSON.parse(storedUserInfo);
      // store   
      store.commit('setAuth', {
        token: storedToken,
        user: userData
      });
      // // console.log('[AUTH] localStorage   ');
      next();
      return;
    } catch (error) {
      console.error('Stored user info parsing error:', error);
      //    localStorage 
      localStorage.removeItem('access_token');
      localStorage.removeItem('user_info');
    }
  }
  
  // URL OAuth      
  const urlParams = new URLSearchParams(window.location.search);
  const token = urlParams.get('token');
  const hash = window.location.hash;
  
  if (token || (hash && (hash.includes('id_token')))) {
    isProcessingOAuth = true;
    next();
    return;
  }
  
  // OAuth   
  const hasProcessedOAuth = sessionStorage.getItem('sso_processed') === 'true';
  const isOAuthProcessing = sessionStorage.getItem('oauth_processing') === 'true';
  
  if (hasProcessedOAuth || isOAuthProcessing) {
    // OAuth     
    // // console.log('[AUTH] OAuth  /  -  ');
    
    // localStorage     
    const storedToken = localStorage.getItem('access_token');
    const storedUserInfo = localStorage.getItem('user_info');
    
    if (storedToken && storedUserInfo) {
      // localStorage     
      // // console.log('[AUTH] localStorage    -  ');
      
      // store  
      try {
        const userData = JSON.parse(storedUserInfo);
        store.commit('setAuth', {
          token: storedToken,
          user: userData
        });
      } catch (error) {
        console.error('User info parsing error:', error);
      }
      
      next();
      return;
    } else {
      // OAuth         
      // // console.log('[AUTH] OAuth   -   ');
      let retryCount = 0;
      const maxRetries = 6; //  3  (500ms * 6)
      
      const checkAuthState = () => {
        retryCount++;
        const currentToken = localStorage.getItem('access_token');
        const currentUserInfo = localStorage.getItem('user_info');
        
        if (currentToken && currentUserInfo) {
          // // console.log('[AUTH]    -  ');
          try {
            const userData = JSON.parse(currentUserInfo);
            store.commit('setAuth', {
              token: currentToken,
              user: userData
            });
          } catch (error) {
            console.error('User info parsing error:', error);
          }
          next();
        } else if (retryCount >= maxRetries) {
          // // console.log('[AUTH]    - OAuth    ');
          //   OAuth  
          sessionStorage.removeItem('oauth_processing');
          sessionStorage.removeItem('sso_processed');
          next(false);
          // samsung SSO 
          setTimeout(() => {
            try {
              window.location.replace('http://localhost:8000/api/auth/auth_sh');
            } catch (error) {
              try {
                window.location.href = 'http://localhost:8000/api/auth/auth_sh';
              } catch (error2) {
                console.error('SSO  :', error2);
              }
            }
          }, 100);
        } else {
          setTimeout(checkAuthState, 500);
        }
      };
      
      setTimeout(checkAuthState, 500);
      return;
    }
  }
  
  //    samsung SSO 
  // // console.log('[AUTH]   - SSO ');
  next(false);
  
  // samsung SSO 
  setTimeout(() => {
    try {
      window.location.replace('http://localhost:8000/api/auth/auth_sh');
    } catch (error) {
      try {
        window.location.href = 'http://localhost:8000/api/auth/auth_sh';
      } catch (error2) {
        alert(' .    .');
      }
    }
  }, 100);
};

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { 
      path: '/', 
      component: Home
    },
    { 
      path: '/history', 
      component: ChatHistory
    },
    { 
      path: '/admin', 
      component: Home
    },
    { 
      path: '/oauth_callback', 
      redirect: to => {
        // Get hash from query string if available
        const hash = to.hash || to.query.hash || window.location.hash;
        if (hash) {
          // Process OAuth callback in the app
          const hashParams = new URLSearchParams(hash.substring(1));
          const access_token = hashParams.get('access_token');
          const id_token = hashParams.get('id_token');
          
          if (access_token && id_token) {
            // Extract user info from token and log in
            try {
              const base64Url = id_token.split('.')[1];
              const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
              const jsonPayload = decodeURIComponent(atob(base64).split('').map(c => {
                return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
              }).join(''));
              
              const payload = JSON.parse(jsonPayload);
              
              // Set user info in store
              const user = {
                username: payload.name || "User",
                mail: payload.email || "",
                deptname: payload.deptname || "",
                loginid: payload.sub,
                id: payload.sub
              };
              
              //     
              fetch('http://localhost:8000/api/auth/token', {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                  username: user.username,
                  id_token: id_token,
                  // access_token  -   
                  is_sso: true
                })
              })
              .then(response => {
                if (!response.ok) {
                  return response.json().then(errorData => {
                    console.warn("Backend authentication failed:", errorData);
                    throw new Error(errorData.detail || 'Backend authentication failed');
                  });
                }
                return response.json();
              })
                      .then(data => {
          //       
          const backendUser = data.user || user;
          
          //    localStorage 
          //  JWT   
          store.commit('setAuth', {
            token: data.access_token,  //  JWT 
            user: backendUser
          });
          
          // localStorage JWT   (API )
          localStorage.setItem('access_token', data.access_token);
          localStorage.setItem('user_info', JSON.stringify(backendUser));
          
          // // console.log('[AUTH]  JWT   :', {
          //   token: data.access_token.substring(0, 20) + '...',
          //   user: backendUser.username
          // });
        })
                      .catch((error) => {
          console.error('Backend authentication failed:', error);
          //      
          alert('  .  .');
          //     
          // // console.log('   -  ');
        })
              .finally(() => {
                //   
                store.commit('setConversations', []);
                store.commit('setCurrentConversation', null);
                store.dispatch('fetchConversations')
                  .catch(error => {
                    console.error('  :', error);
                  })
                  .finally(() => {
                    // OAuth      
                    isProcessingOAuth = false; // OAuth  
                    hasProcessedOAuth = true; // OAuth    
                    
                    // OAuth    
                    sessionStorage.setItem('sso_processed', 'true');
                    sessionStorage.removeItem('oauth_processing');
                    
                    //     
                    if (store.state.isAuthenticated && localStorage.getItem('access_token')) {
                      router.push('/');
                    } else {
                      try {
                        // // console.log('  -  ');
                      } catch (error) {
                        try {
                          // // console.log('OAuth   -  ');
                        } catch (error2) {
                          // // console.log('OAuth   -  ');
                        }
                      }
                    }
                  });
              });
            } catch (error) {
              // console.error("OAuth Token Processing Error:", error);
              isProcessingOAuth = false; //    
            }
          }
        }
        
        // After processing OAuth data, redirect to home
        return '/';
      }
    }
  ]
});

// Check for token in URL (from OAuth redirect)
const checkForAuthToken = () => {
  //    OAuth  
  const isLogoutRedirect = sessionStorage.getItem('logout_redirect') === 'true';
  if (isLogoutRedirect) {
    // // console.log('[AUTH]   - OAuth  ');
    sessionStorage.removeItem('logout_redirect'); //  
    return;
  }
  
  //  OAuth      
  // // console.log('[AUTH] checkForAuthToken :', {
  //   hasProcessedOAuth,
  //   isProcessingOAuth,
  //   currentUrl: window.location.href
  // });
  
  if (hasProcessedOAuth) {
    // // console.log('[AUTH] OAuth  ,   ');
    return;
  }
  
  //        (  )
  if (sessionStorage.getItem('oauth_processing') === 'true') {
    // // console.log('[AUTH]  OAuth   ');
    sessionStorage.removeItem('oauth_processing');
  }
  
  // OAuth   
  isProcessingOAuth = true;
  
  // 1.     ( )
  const urlParams = new URLSearchParams(window.location.search);
  const token = urlParams.get('token');
  
  // 2. URL  samsung OAuth   ( ,   )
  if (window.location.hash) {
    // URL  
    const hashParams = new URLSearchParams(window.location.hash.substring(1));
    const idToken = hashParams.get('id_token');
    const state = hashParams.get('state');
    
    if (idToken && state) {
      // OAuth    
      // // console.log('[AUTH] OAuth   -  ');
      sessionStorage.setItem('oauth_processing', 'true');
      sessionStorage.removeItem('sso_processed'); //    
      
      //  
      const url = new URL(window.location);
      url.hash = '';
      window.history.replaceState({}, document.title, url);
      
      processOAuthToken(idToken, state);
      
      // OAuth   
      hasProcessedOAuth = true;
      isProcessingOAuth = false;
      return; //  
    }
  }
  
  // 3.      ( )
  if (token) {
    // Clear URL parameters but keep the path
    const url = new URL(window.location);
    url.search = '';
    window.history.replaceState({}, document.title, url);
    
    // Login with token (OAuth    )
    if (!hasProcessedOAuth) {
      store.dispatch('loginWithToken', token)
        .then(() => {
          hasProcessedOAuth = true;
          isProcessingOAuth = false;
          // // console.log('[AUTH]   ,  ');
          router.push('/');
        })
        .catch(error => {
          console.error("Auto-login failed:", error);
          isProcessingOAuth = false;
          router.push('/login');
        });
    } else {
      // // console.log('[AUTH] OAuth  ,   ');
      isProcessingOAuth = false;
    }
  } else {
    //     
    isProcessingOAuth = false;
  }
};

//    
router.beforeEach((to, from, next) => {
  // OAuth   
  if (to.path === '/oauth_callback') {
    next();
    return;
  }
  
  //   
  const protectedRoutes = ['/', '/history', '/admin'];
  if (protectedRoutes.includes(to.path)) {
    requireAuth(to, from, next);
  } else {
    next();
  }
});

// Execute on app start
checkForAuthToken();

const app = createApp(App);

// Vue   ( )
if (process.env.NODE_ENV === 'development') {
  // Vue   
  app.config.devtools = true;
  app.config.debug = true;
  
  //   
  app.config.performance = true;
  
  //    ()
  app.config.errorHandler = (error, instance, info) => {
    console.error('[Vue Error Handler]', error);
    console.error('[Vue Error Info]', info);
    console.error('[Vue Instance]', instance);
  };
  
  //    ()
  app.config.warnHandler = (msg, instance, trace) => {
    console.warn('[Vue Warning]', msg);
    console.warn('[Vue Trace]', trace);
  };
  
  // console.log('[Vue Debug] Vue    ');
}

app.use(store);
app.use(router);

//      
const mountedApp = app.mount('#app');

if (process.env.NODE_ENV === 'development') {
  //  Vue    
  window.__VUE_APP__ = mountedApp;
  window.__VUE_STORE__ = store;
  window.__VUE_ROUTER__ = router;
  
  // console.log('[Vue Debug]     ');
  // console.log('[Vue Debug] window.__VUE_APP__, window.__VUE_STORE__, window.__VUE_ROUTER__  ');
} 
